body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  width: max(50vw, 90vmin);
}

.App footer {
  font-style: italic;
}

.App .dont-click {
  cursor: not-allowed;
}

.App figure {
  background-color: whitesmoke;
  padding: 2vmin;
  text-align: center;
}

.App figure h2 {
  font-size: 1em;
  font-weight: normal;
}

.App figure h2::before {
  content: "Figure: ";
  font-weight: bold;
}
